.photo-editor__desc {
    .font-style(14px; 18px; normal);
}

.photo-editor__box {
    background-color: @light-grey;
    padding-top: 20px;
    padding-bottom: 40px;
    @media (min-width:@tablet-size) {
        padding-top: 55px;
    padding-bottom: 55px;
    }
}

.photo-editor__photo {
    display: flex;
}

.photo-editor__picture {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}

.photo-editor__picture-image {
    object-fit: cover;
    // height: 100%;
    // margin: 0 auto;
}

.editor-form {
    display: grid;
    grid-template-columns: 1fr;
    @media (min-width:@tablet-size) {
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
}

.photo-editor-toggle {
    padding-top: 25px;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-direction: column;
}

.photo-editor-toggle__tool {
    border: none;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    padding-top: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid fade(@blue, 10%);
}

.photo-editor-toggle__button {
    border: none;
    cursor: pointer;
    background-color: transparent;
    width: 50px;
    height: 50px;
    // .photo-editor-toggle__icon {
    //     fill: @strong-pink;
