    //         opacity: 1;
    //     } 
    // }
}

.photo-editor-toggle__icon {
    fill: @strong-pink;
} 


.photo-editor-toggle__range {
    flex-grow: 1;
    background-color: @blue;
    // color: @white;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: auto;
    height: 2px;
   & ::-webkit-slider-runnable-track {
    appearance: none;
    height: 2px;
    background-color: @blue;
   }
   &::-webkit-slider-thumb {
    appearance: none;
    width: 12px;
    height: 12px;
    background-color: @white;
    border: 2px solid @strong-pink;
    border-radius: 12px;
   }
   &:active::-webkit-slider-thumb {
    background-color: @strong-pink;
   }
//    &::-webkit-slider-thumb {
//     appearance: none;
//     width: 12px;
//     height: 12px;
//     background-color: @white;
//     border: 2px solid @strong-pink
//    }
   & ::-moz-range-track {
    appearance: none;
    height: 2px;
    background-color: @blue;
   }
   &::-moz-range-thumb {
    appearance: none;
    width: 12px;
    height: 12px;
    background-color: @white;
    border: 2px solid @strong-pink;
    border-radius: 12px;
   }
   &:active::-moz-range-thumb {
    background-color: @strong-pink;
   }
}