
.contest-form__button {
    .font-style(18px; 30px; bold);
    border: none;
    background-color: @form-button;
    color: @white;
    text-transform: uppercase;
    min-width: 276px;
    padding: 20px 45px;
    border-radius: 4px;
    cursor: pointer;
    @media (min-width: @tablet-size) {
        padding: 20px 55px;
    }
    &:disabled {
        background-color: @grey;
    }
    &:hover {
        background-color: @dark-form-button;
    }
    &:focus {
        background-color: @dark-form-button;
    }
    &:active {
        background-color: @dark-form-button;
        color: fade(@white, 30%);
    }
}

