.contest-form {
    background-color: @white;
    overflow: hidden;
    display: grid;
    grid-template-areas: "name"
                         "achive"
                         "contacts"
                         "app"
                         "text"
                         "button" 
                         "comment";
    @media (min-width: @tablet-size) {
        gap: 70px;
        grid-template-areas: "name name"
                             "app app"
                             "achive achive"
                             "contacts contacts"
                             "text text"
                             "button comment";
        
    }
    @media (min-width: @desktop-size) {
        grid-template-areas: "name app"
                             "contacts contacts"
                             "achive achive"
                             "text text"
                             "button comment";
    }
}




.form-area {
    @media (max-width: @mobil-size) {
        display: block;
        position: relative;
    }
}

.contest-form-user-name {
    grid-area: name;
    border: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 25px;
    @media (min-width: @tablet-size) {
        gap: 55px;
    }
}

.contest-form-user-name__block {
    margin: 0;
    flex-grow: 1;
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    align-items: center;
    @media (min-width: @tablet-size) {
        flex-wrap: nowrap;
        margin: 0;
        gap: 20px;
    }
    .form-area__box {
        width: 100%;
        padding: 15px 20px;
        box-sizing: border-box;
    }
}



.contest-form-app {
    grid-area: app;
    border: none;
    margin: 40px 0;
    padding: 0;
    @media (min-width: @tablet-size) {
        border: 2px solid @grey;
        border-radius: 3px;
        margin: 0;
    }
    @media (min-width: @desktop-size) {
        margin: 0;
        padding: 0 60px;
    }
}

.app-list {
    margin-top: 50px;
    display: grid;
    gap: 45px;
    @media (min-width: @tablet-size) {
        margin: 60px 0;
    }
    @media (min-width: @desktop-size) {
        margin: 65px 0;
    }
}

.app-item {
    padding-left: 70px;
    @media (min-width: @tablet-size) {
        padding-left: 120px;
    }
    @media (min-width: @desktop-size) {
        padding-left: 60px;
    }
}

.form-area__radio {
    position: relative;
}

.app-item__radio + .form-area__radio::before {
    content: "";
    position: absolute;
    left: -66px;
    top: -7px;
    width: 35px;
    height: 35px;
    border: 2px solid @grey;
    border-radius: 20px;
    z-index: 1;
    cursor: pointer;
}

.app-item__radio:hover + .form-area__radio::before {
    border: 2px solid @blue;
}

.app-item__radio:focus + .form-area__radio::before {
    border: 2px solid @blue;
}

.app-item__radio:active + .form-area__radio::before {
    border: 2px solid @strong-pink;
}

.app-item__radio:checked + .form-area__radio::after {
    content: "";
    position: absolute;
    left: -54px;
    top: 5px;
    width: 15px;
    height: 15px;
    background-color: @strong-pink;
    border-radius: 15px;
    z-index: 1;
}


.contest-form-user-contacts {
    border: none;
    padding: 30px 0;
    grid-area: contacts;
    display: flex;
    flex-wrap: wrap;
    @media (min-width: @tablet-size) {
        border: 2px solid @grey;
        border-radius: 3px;
    }
    @media (min-width: @desktop-size) {
        flex-direction: row;
        flex-wrap: nowrap;
    }
    .form-area__name--legend {
        @media (max-width: @mobil-size) {
            display: none;
        }
    }
}

.contest-form-user-contacts__block {
    margin: 15px 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    @media (min-width: @tablet-size) {
        margin: 20px 60px;
        width: 100%;
    }
    @media (min-width: @desktop-size) {
        margin: 0 30px;
    }
    .form-area__name {
        padding-right: 20px;
        @media (min-width: @tablet-size) {
            order: 2;
            padding-right: 0;
            padding-left: 20px;
        }
    }
    .form-area__box {
        width: 100%;
        margin: 15px 0;
        padding: 15px 20px;
        box-sizing: border-box;
        flex-grow: 1;
    }
}

.contest-form-user-contacts__block--icon {
    position: relative;
    @media (min-width: @tablet-size) {
        &::after {
            position: absolute;
            content: "";
            top: 16px;
            right: 0;
            width: 60px;
            height: 61px;
            background-color: @grey;
        }
        &-phone::after {
            background-image: url(../img/form-phone.svg);
            background-repeat: no-repeat;
            background-position: 50%;
        }
        &-email::after {
            background-image: url(../img/form-email.svg);
            background-repeat: no-repeat;
            background-position: 50%;
        }
    }
}

.contest-form-achive {
    grid-area: achive;
    border: none;
    margin: 40px 0;
    @media (min-width: @tablet-size) {
        border: 2px solid @grey;
        border-radius: 3px;
        margin: 0;
    }
}

.achive-list {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 45px;
    @media (min-width: @tablet-size) {
        margin: 60px 0;
    }
    @media (min-width: @desktop-size) {
        // grid-template-columns: 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr;
        // grid-template-rows: 1fr 1fr;
        margin: 65px 0;
        grid-auto-flow: row;
        column-gap: 0;
    }
}

.achive-item {
    padding-left: 55px;
    @media (min-width: @tablet-size) {
        padding-left: 110px;
    }
    @media (min-width: @desktop-size) {
        padding-left: 110px;
    }
}

.form-area__checkbox {
    position: relative;
}

.achive-item__checkbox + .form-area__checkbox::before {
    content: "";
    position: absolute;
    left: -57px;
    top: -7px;
    width: 35px;
    height: 35px;
    border: 2px solid @grey;
    border-radius: 1px;
    z-index: 1;
    cursor: pointer;
}

.achive-item__checkbox:hover + .form-area__checkbox::before {
    border: 2px solid @blue;
}

.achive-item__checkbox:focus + .form-area__checkbox::before {
    border: 2px solid @blue;
}

.achive-item__checkbox:active + .form-area__checkbox::before {
    border: 2px solid @strong-pink;
}

.achive-item__checkbox:checked + .form-area__checkbox::after {
    content: "";
    position: absolute;
    left: -50px;
    top: 4px;
    width: 22px;
    height: 16px;
    background-image: url(../img/checkbox.svg);
    background-repeat: no-repeat;
}

.form-area__contacts {
    position: relative;
}

