.contest-form-user-text {
    grid-area: text;
    border: none;
    padding: 20px 0;
    margin: 40px 0;
    @media (min-width: @tablet-size) {
        padding: 50px 60px;
        border: 2px solid @grey;
        border-radius: 3px;
        margin: 0;
    }
    .form-area__name {
        @media (min-width: @tablet-size) {
        }
    }
    .form-area__box {
        width: 100%;
        padding: 15px 20px;
        box-sizing: border-box;
        min-height: 200px;
        @media (min-width: @tablet-size) {
            min-height: 190px;
        }
        @media (min-width: @desktop-size) {
            min-height: 125px;
        }
    }
}

.contest-form-user-text__block {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding: 0;
    margin: 0;
    @media (min-width: @tablet-size) {
        border: 2px solid @grey;
        border-radius: 3px;
    }
    .form-area__name {
        width: 100%;
    }
    .form-area__box {
        width: 100%;
        padding: 15px 20px;
        box-sizing: border-box;
    }
}

.contest-form-submit {
    grid-area: button;
    padding: 0;
    margin-bottom: 25px;
    @media (min-width: @tablet-size) {
        margin: 0;
    }
}

.contest-form-comment {
    grid-area: comment;
    text-transform: uppercase;
    text-align: center;
    @media (min-width: @tablet-size) {
        text-align: right;
    }
}

.note-form {
    &--desktop {
        @media (max-width: @mobil-size) {
            display: none;
        }
    }
}

.form-area__name {
    .font-style(14px; 18px; normal);
    color: @strong-pink;
    text-transform: uppercase;
    text-align: center;
    padding: 0 20px;
}

.form-area__caption {
    .font-style(18px; 30px; normal);
    color: @blue;
    &::placeholder {
        color: @blue;
    }
}

.form-area__box {
    border: 2px solid @grey;
    border-radius: 3px;
    cursor: pointer;
    &:hover {
        border: 2px solid @blue;
    }
    &:focus {
        border: 2px solid @strong-pink;
    }
    &:focus-visible {
        border: 2px solid @strong-pink;
    }
    &:active {
        border: 2px solid @strong-pink;
    }
}

.form-area__text {
    .font-style(18px; 30px; normal);
    max-height: 197px;
    min-height: 124px;
    &::placeholder {
        .font-style(18px; 30px; normal);
        font-family: Open Sans;
    }
}

.asterisk {
    color: @strong-pink;
}
