.page-header {
    width: 100%;
    background-color: @dark-blue;
    background-image: url(../img/header-bg-mobil.png);
    @media (min-resolution: 2dppx) {
        background-image: url(../img/header-bg-mobil@2x.png);
    }
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top -50px left 50%;
    @media (min-width:@tablet-size) {
        background-position: top 0 left 50%;
        background-image: url(../img/header-bg-tablet.png);
        @media (min-resolution: 2dppx) {
            background-image: url(../img/header-bg-tablet@2x.png);
        }
    } 
    @media (min-width: @desktop-size) {
        background-position: top;
    object-fit: cover;
        background-image: url(../img/header-bg-desktop.png);
        @media (min-resolution: 2dppx) {
            background-image: url(../img/header-bg-desktop@2x.png);
        }
    
    }
}
